/* Base styles */
/* See https://github.com/sindresorhus/github-markdown-css */

*, *:before, *:after {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.reveal .slides>section, .reveal .slides>section>section {
  padding: 0;
}

.slides .highlight {
  /*background: #fff;*/
  margin: 0 auto;
}

.slides .highlight .hl-o {
  background-color: #FFC271;
}

.slides .highlight .hl-g {
  background-color: #8DDB77;
}

.slides .highlight .fade {
  opacity: 0.1;
}

.slides .highlight pre {
  font: 50px Menlo;
  line-height: 1.45;
  overflow: auto;
  word-break: normal;
  word-wrap: normal;
  text-align: left;
}

.slides .highlight.ttiny pre {
  font-size: 20px;
}

.slides .highlight.tiny pre {
  font-size: 30px;
}

.slides .highlight.ssmall pre {
  font-size: 35px;
}

.slides .highlight.small pre {
  font-size: 40px;
}

.slides .highlight.big pre {
  font-size: 60px;
}

.slides .highlight.center {
  text-align: center;
}

.slides .highlight.center pre {
  display: inline-block;
  width: auto;
}

.slide-code {
  width: 1440px;
  height: 900px;
  position: relative;
  background-size: contain;
  border: 1px solid #bbb !important;
}

.slide-code .highlight {
  position: absolute;
  top: 80px;
}

.slide-code .highlight.left-half {
  width: 50%;
  left: 0;
}

.slide-code .highlight.left-small {
  width: 40%;
  left: 0;
}

.slide-code .highlight.left-big {
  width: 60%;
  left: 0;
}

.slide-code .highlight.right-half {
  width: 50%;
  right: 0;
}

.slide-code .highlight.right-half-top {
  width: 50%;
  right: 0;
  height: 50%;
  top: 0;
}

.slide-code .highlight.right-half-btm {
  width: 50%;
  right: 0;
  height: 50%;
  top: 50%;
}

.slide-code .highlight.right-small {
  width: 40%;
  right: 0;
}

.slide-code .highlight.right-big {
  width: 60%;
  right: 0;
}


.slides .highlight .hljs-literal {
  color: #DA672B;
  /*font-weight: bold;*/
}


/*
github.com style (c) Vasily Polovnyov <vast@whiteants.net>
*/

.slides .hljs {
  display: block;
  overflow-x: auto;
  color: #333;
}

.slides .hljs-comment,
.slides .hljs-template_comment,
.slides .diff .hljs-header,
.slides .hljs-javadoc {
  color: #998;
  font-style: italic;
}

.slides .hljs-keyword,
.slides .css .rule .hljs-keyword,
.slides .hljs-winutils,
.slides .javascript .hljs-title,
.slides .nginx .hljs-title,
.slides .hljs-subst,
.slides .hljs-request,
.slides .hljs-status {
  color: #333;
  font-weight: bold;
}

.slides .hljs-number,
.slides .hljs-hexcolor,
.slides .ruby .hljs-constant {
  color: #099;
}

.slides .hljs-string,
.slides .hljs-tag .hljs-value,
.slides .hljs-phpdoc,
.slides .tex .hljs-formula {
  color: #d14;
}

.slides .hljs-title,
.slides .hljs-id,
.slides .coffeescript .hljs-params,
.slides .scss .hljs-preprocessor {
  color: #900;
  font-weight: bold;
}

.slides .javascript .hljs-title,
.slides .lisp .hljs-title,
.slides .clojure .hljs-title,
.slides .hljs-subst {
  font-weight: normal;
}

.slides .hljs-class .hljs-title,
.slides .haskell .hljs-type,
.slides .vhdl .hljs-literal,
.slides .tex .hljs-command {
  color: #458;
  font-weight: bold;
}

.slides .hljs-tag,
.slides .hljs-tag .hljs-title,
.slides .hljs-rules .hljs-property,
.slides .django .hljs-tag .hljs-keyword {
  color: #000080;
  font-weight: normal;
}

.slides .hljs-attribute,
.slides .hljs-variable,
.slides .lisp .hljs-body {
  color: #008080;
}

.slides .hljs-regexp {
  color: #009926;
}

.slides .hljs-symbol,
.slides .ruby .hljs-symbol .hljs-string,
.slides .lisp .hljs-keyword,
.slides .tex .hljs-special,
.slides .hljs-prompt {
  color: #990073;
}

.slides .hljs-built_in,
.slides .lisp .hljs-title,
.slides .clojure .hljs-built_in {
  color: #0086b3;
}

.slides .hljs-preprocessor,
.slides .hljs-pragma,
.slides .hljs-pi,
.slides .hljs-doctype,
.slides .hljs-shebang,
.slides .hljs-cdata {
  color: #999;
  font-weight: bold;
}

.slides .hljs-deletion {
  background: #fdd;
}

.slides .hljs-addition {
  background: #dfd;
}

.slides .diff .hljs-change {
  background: #0086b3;
}

.slides .hljs-chunk {
  color: #aaa;
}